.a-multilevel-menu {
    list-style: none;
    padding: 0 0;
    margin: 0 0;
    display: inline-block;

    // Second level
    ul {
        list-style: none;
        padding: $horizontal-base-width / 2 $vertical-base-width;
        margin: 0 0;
        background-color: white;

        min-width: 66vw;
        max-width: 90vw;
        
        overflow: hidden;
        height: 0;
        opacity: 0;


        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: $column-gap;
        grid-row-gap: $column-gap / 2;

        position: absolute;
        z-index: 4;

        transition: all .25s ease-in-out;

        li {
            transition: all .25s ease-in-out;
            opacity: 0;
            grid-column: span 1;
        }

        @for $i from 1 through 25 {
            li:nth-of-type(6n + #{$i}) {
               transition-delay: $i * 0.025s;
            }
        }
    }

    //
    // Show menu
    //
    &:hover,
    &:focus {
        ul {
            height: auto;
            opacity: 1;
            box-shadow: 8px 8px 7px -6px rgba(0,0,0,0.25);

            li {
                opacity: 1;
            }
        }
    }
}

//
// Media query md up
//
@include media-breakpoint-up(lg) {
    .a-multilevel-menu {
        ul {
            min-width: auto;
        }
    }
}