.o-main-footer {
    &__row-one {
        background-color: $footer-nav;
        padding-top: $horizontal-base-width * 2;
        padding-bottom: $horizontal-base-width * 2;

        padding-left: $vertical-base-width;
        padding-right: $vertical-base-width;
    }
    &__row-two {
        background-color: $footer-foot;
        padding-top: $horizontal-base-width;
        padding-bottom: $horizontal-base-width * 2;

        padding-left: $vertical-base-width;
        padding-right: $vertical-base-width;
    }

    &__nav {
        &__logo {
            margin-bottom: $horizontal-base-width * 2;
        }

        &__header {
            margin-bottom: $horizontal-base-width / 2;
        }

        &__menu-one {
            margin-bottom: $horizontal-base-width * 2;
        }
    }

    &__foot {
        &__company {
            font-size: 1.25em;
            text-transform: uppercase;
            margin-bottom: $horizontal-base-width * 2;
            color: $text-color;
        }

        &__regulations {
            font-size: 1em;
            text-transform: uppercase;
        }
    }

}

//
// Media query md up
//
@include media-breakpoint-up(lg) {
    .o-main-footer {
        &__row-two {
            padding-bottom: $horizontal-base-width * 3;
        }

        &__nav {
            width: 100%;
            display: inline-grid;
        
            grid-template-columns: repeat(12, 1fr);
            grid-column-gap: $column-gap;

            max-width: $desktop-layout-width;
            

            &__logo {
                grid-column: span 4;
                margin-bottom: 0;

                > * {
                    text-align: left;
                }
            }

            &__menu-one {
                grid-column: span 5;
                margin-bottom: 0;
            }

            &__menu-two {
                grid-column: span 3;
            }
        }

        // Last part footer
        &__foot {
            display: inline-grid;
            max-width: $desktop-layout-width;
            width: 100%;
            grid-template-columns: repeat(12, 1fr);
            grid-row-gap: $row-gap;
            grid-column-gap: $column-gap;

            &__company {
                grid-column: span 4;

                font-size: 1.250em;
                text-transform: uppercase;
                margin-bottom: 0;

                text-align: left;

                padding-top: $horizontal-base-width / 2;
            }
    
            &__regulations {
                grid-column: span 8;

                font-size: 1.250em;
                text-transform: uppercase;

                text-align: right;
            }
        }

    }
}