.t-article {
    &__header {
        padding-top: $horizontal-base-width;
        padding-bottom: $horizontal-base-width;

        padding-left: $vertical-base-width;
        padding-right: $vertical-base-width;
    }

    //
    // Articles
    //
    &__articles-block {
        padding-left: $vertical-base-width;
        padding-right: $vertical-base-width;

        // padding-top: $horizontal-base-width * 1.5;
        padding-bottom: $horizontal-base-width * 1.5;
    }

    //
    // Products
    //
    &__products {
        margin-bottom: $horizontal-base-width * 2;
    }

    &__advert-articles {
        padding-left: $vertical-base-width;
        padding-right: $vertical-base-width;
        padding-bottom: $horizontal-base-width * 2;

        &__advert {
            margin-bottom: $horizontal-base-width;

            > * {
                width: 100%;
            }
        }
    }
}

//
// Media query md up
//
@include media-breakpoint-up(md) {
    .t-article {
        &__header {
            > * {
                max-width: $desktop-layout-width;
            }

            padding-left: 0;
            padding-right: 0;
        }

        &__articles-block {
            // padding-top: $horizontal-base-width * 2;
    
            > * {
                max-width: $desktop-layout-width;
            }
        }
    } 
}

//
// Media query lg up
//
@include media-breakpoint-up(lg) {
    .t-article {
        &__header {
            padding-top: $horizontal-base-width;
            padding-bottom: $horizontal-base-width;
        }
    }     
}