/* o-header component file */
.o-main-header {
    width: 100%;
    display: inline-grid;

    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 1fr;

    grid-row-gap: 0;
    grid-column-gap: $column-gap;

    &__logo-container {
        grid-column: span 12;

        text-align: left;

        &__logo {
            width: 49%;
            display: inline-block;
            vertical-align: middle;
        }

        &__social-box {
            width: 49%;
            display: inline-block;
            vertical-align: middle;
            text-align: right;
        }
    }

    //
    // Tools container
    //
    &__tools {
        grid-column: span 12;
        text-align: left;

        display: grid;
        grid-template-columns: repeat(12, 1fr);

        grid-row-gap: $row-gap;
    }
    
    &__search-box {
        grid-column: span 12;
        grid-row-start: 2;
    }

    &__social-box {
        grid-column: span 12;
        grid-row-start: 1;

        text-align: center;

        > * { display: none }
    }

    &__menu {
        grid-column: span 12;
    } 

    &__social-icon {
        margin-left: $horizontal-base-width / 4;
        margin-right: $horizontal-base-width / 4;
    }

    //
    // Menu links
    //

    span.o-main-header__menu-link {
        cursor: default;
    }

    &__menu-link {
        // font-size: 1.563em;
        font-size: 1em;
        color: $menu-color;
        text-decoration: none;
        display: block;
        line-height: 120%;
        font-weight: 500;

        padding-top: .25em;

        transition: color .25s ease-in-out;

        &:focus,
        &:hover {
            color: lighten($menu-color, 20)
        }

        //
        // Modifiers
        //
        &.-down {
            position: relative;
            padding-right: 1.25em;

            &::after {
                content: "";
                position: absolute;
                display: block;
                width: 1em;
                height: 100%;
                top: 0;
                right: 0;
                background-image: url($image-path + "arrow_down.png");
                background-repeat: no-repeat;
                background-position: center center;
                transition: opacity .25s ease-in-out;
            }

            &:hover::after {
                opacity: .5;
            }
        }
        
        &.-main {
            line-height: 150%;
            font-size: 1.25em;
        }
    }
}

//
// Media query md up
//
@include media-breakpoint-up(md) { 
    .o-main-header {
        &__logo-container {
            align-self: center;

            grid-column-start: 1;
            grid-column-end: 5;

            &__logo {
                width: 100%;
                display: block;
            }
    
            &__social-box {
                display: none;
            }
        }

        //
        // Tools container
        //
        &__tools {
            grid-column-start: 7;
            grid-column-end: 13;

            text-align: left;

            display: grid;

            grid-column-gap: 0;
            grid-template-columns: minmax(auto, 90%) minmax(100px, 10%);

            grid-row-gap: $row-gap * 2;
        }
        
        &__search-box {
            grid-column: span 1;
            grid-row-start: initial;
        }

        &__social-box {
            text-align: right;
            grid-column: span 1;
            grid-row-start: initial;

            > * {
                display: inline-block;
            }
        }

        &__menu {
            grid-column: span 2;
        } 

        //
        // Menu links
        //
        &__menu-link {
            display: inline-block;
            margin-right: $vertical-base-width;
        }
    } 
}

//
// Media query lg up
//
@include media-breakpoint-up(lg) { 
    .o-main-header {
        &__logo-container {
            align-self: normal;

            grid-column-start: 1;
            grid-column-end: 4;
        }

        &__tools {
            grid-column-start: 7;
            grid-column-end: 13;
        } 


        &__menu-link {
            font-size: 1.25em;

            &.-main {
                font-size: 1.563em;
            }
        }
    } 
}