.o-slider {
    &__slide {
        width: 100%;
        display: inline-block;
    }

    //
    // Modifiers
    //
    &.-hide-indicator {
        .flickity-page-dots {
            display: none;
        }
    }

    &.-custom-navigation {
        .flickity-button {
            z-index: 3;
            border-radius: 0;

            // Size
            height: 42px;
            width: 25px;

            padding: 0 0;

            background-image: url($image-path + 'arrow_navigation.svg');
            background-color: transparent;
            background-size: 100% 100%;
            background-repeat: no-repeat;
            background-position: center;

            transition: opacity .5s ease-in-out;
            opacity: .5;

            .flickity-button-icon {
                display: none;
            }

            &.previous {
                transform: scaleX(-1) translateY(-50%);
            }

            &:hover {
                opacity: 1;
            }
        }
    }
}

//
// Media query md up
//
@include media-breakpoint-up(md) { 
    .o-slider {
        &__slide {
            width: 50%;
        }
    }    
}

//
// Media query lg up
//
@include media-breakpoint-up(lg) { 
    .o-slider {
        &__slide {
            width: 25%;
        }

        //
        // Modifiers
        //
        &.-mask-fringe-slides {
            &::before,
            &::after {
                content: "";
                display: block;
                position: absolute;
                top: 0;
                background-color: transparentize($overlay-color, .7);
                
                width: 25%;
                height: 100%;

                z-index: 2;
            }

            &::before {
                left: 0;
            }

            &::after {
                right: 0;
            }
        }

        &.-custom-navigation {
            .flickity-button {

                // Size
                height: 84px;
                width: 50px;

                &.previous {
                    left: 25%;
                    transform: scaleX(-1) translateY(-50%) translateX(120%);
                }

                &.next {
                    right: 25%;
                    transform: translateY(-50%) translateX(120%);
                }
            }
        }
    }    
}