.m-column-menu {
    &__link {
        display: block;
        text-decoration: none;
        color: $text-color;

        padding: $horizontal-base-width / 2 0;

        text-transform: uppercase;

        transition: opacity .25s ease-in-out;

        &:hover,
        &:focus {
            opacity: .5;
        }
    }
}


//
// Media query md up
//
@include media-breakpoint-up(md) {
    .m-column-menu {
        text-align: left;

        &__link {
            font-size: 1.250em;
            padding: $horizontal-base-width / 2 0;
        }

        //
        // Modifiers
        //
        &.-two-columns {
            width: 100%;
            display: inline-grid;
        
            grid-template-columns: repeat(2, 1fr);
            grid-column-gap: $column-gap * 2;
    
            &__link {
                grid-column: span 1;
                text-align: left;
            }
        }
    }
}