/* a-social-icon atom file */
.a-social-icon {
    display: inline-block;
    background-color: $main-color;
    border-radius: 50%;

    width: $vertical-base-width * 2;
    height: $vertical-base-width * 2;
    
    text-align: center;
    color: White;

    background-position: center center;
    background-repeat: no-repeat;
    background-size: 80% auto;

    vertical-align: middle;

    transition: background-color .25s ease-in-out;

    &:hover {
        background-color: darken($main-color, $amount: 25);
    }

    //
    // Modifiers
    //
    &.-facebook {
        background-image: url($image-path + "facebook.svg");
        
    }

    &.-youtube {
        background-image: url($image-path + "youtube.svg");
        background-size: 100% auto;
    }
}