/* a-button atom file */
.a-button {
    appearance: none;
    height: $vertical-base-width * 2;
    line-height: $vertical-base-width * 2;

    padding-left: $vertical-base-width;
    padding-right: $vertical-base-width;

    font-size: 1em;
    font-weight: 500;
    text-transform: uppercase;

    cursor: pointer;

    background-color: $main-color;
    color: White;
    border: none;

    transition: background-color .25s ease-in-out;

    &:focus, 
    &:hover {
        outline: none;
        background-color: darken($main-color, 20);
    }
}