.a-separated-inline-menu {
    &__item {
        text-decoration: none;
        display: inline-block;
        color: $text-color;
        padding-right: .5em;
        padding-top: $horizontal-base-width / 2;
        padding-bottom: $horizontal-base-width / 2;
        transition: color .25s ease-in-out;

        &:hover {
            color: lighten($text-color, 20);
        }
    }
}

//
// Media query md up
//
@include media-breakpoint-up(lg) {
    .a-separated-inline-menu {
        &__item:not(:last-of-type){    
            &::after {
                content: "|";
                display: inline-block;
                vertical-align: top;
                padding-left: 1em;
                position: relative;
                top: -.1em;
            }
        }
    }
}