.m-article-cover {
    position: relative;
    display: block;
    width: 100%;
    overflow: hidden;
    margin-block-start: 0;
    margin-block-end: 0;
    margin-inline-start: 0;
    margin-inline-end: 0;

    &__bg {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        display: flex;
        z-index: 0;

        img {
            object-fit: cover;
            object-position: center center;
            width: 100%;
            height: 100%;
        }
    }

    &__content {
        width: 100%;
        position: absolute;
        bottom: $horizontal-base-width * 1.5;
        z-index: 2;
        padding: 0 $vertical-base-width;
    }

    &__category {
        margin-bottom: $horizontal-base-width / 2;
    }

    //
    // Modifiers
    //
    &.-aspect-3-1 {
        &::after {
            content: "";
            display: block;
            padding-bottom: 100%;
        }
    }
}

//
// Media query lg up
//
@include media-breakpoint-up(md) {
    .m-article-cover {
        //
        // Modifiers
        //
        &.-aspect-3-1 {
            &::after {
                content: "";
                display: block;
                padding-bottom: 50%;
            }
        }
    }
}


//
// Media query lg up
//
@include media-breakpoint-up(lg) {
    .m-article-cover {
        &__content {
            text-align: left;
            padding: 0 0;
            max-width: $cover-header-width;
            width: 100%;
            display: block;
            left: (100vw - $desktop-layout-width) / 2;
            // margin-left: -250px;
        }

        &__title {
            padding-left: .5em;
            padding-right: .5em;

            &.-center {
                text-align: center;
            }
        }

        //
        // Modifiers
        //
        &.-aspect-3-1 {
            &::after {
                content: "";
                display: block;
                padding-bottom: 33%;
            }
        }
    }
}


//
// Media query xl up
//
@include media-breakpoint-up(xl) {
    .m-article-cover {
        &__content {
            bottom: $horizontal-base-width * 2;
        }

        &__category {
            min-width: 66%;
            padding-left: 1.5em;
        }

        &__title {
            font-size: 2.188em;
            padding-left: 1em;
            padding-right: 1em;

            &.-sm {
                font-size: 1.875em;
            }
        }
    }
}
