/* a-input atom file */
.a-input {
    appearance: none;

    background-color: $main-color-light;
    color: $main-color;
    border: none;

    font-size: 1em;

    height: $horizontal-base-width * 2;
    line-height: $horizontal-base-width * 2;

    padding-right: $vertical-base-width / 2;
    padding-left: $vertical-base-width / 2;

    &:focus {
        outline: none;
    }
}