.o-article-frame-module {
    margin-bottom: $horizontal-base-width * 2;

    &__container {
        padding-left: $vertical-base-width;
        padding-right: $vertical-base-width; 

        overflow: auto;
    }

    &__text {
        text-align: left;
    }

    //
    // Modifiers
    //
    &.-border {
        padding-top: $horizontal-base-width;
        padding-bottom: $horizontal-base-width;

        .o-article-frame-module__container {
            border-top: solid 1px $border-color;
            border-bottom: solid 1px $border-color;
        }

        .o-article-frame-module__text {
            padding-top: $horizontal-base-width;
            padding-bottom: $horizontal-base-width;
        }
    }

    &.-teaser-bg {
        background-color: $teaser-bg;

        .o-article-frame-module__container { 
            padding-top: $horizontal-base-width;
            padding-bottom: $horizontal-base-width;
        }
    }

    &.-frame-bg-blue {
        .o-article-frame-module__container {
            padding-top: 0;
            padding-left: 0;
            padding-right: 0;


            > * {
                color: White;
            }
            background-color: $products-bg;
        }

        .o-article-frame-module__text {
            padding-left: $horizontal-base-width * 2;
            padding-right: $horizontal-base-width * 2;
        }
    }
}

//
// Media query md up
//
@include media-breakpoint-up(lg) {
    .o-article-frame-module {
        &__container {
            padding-left: 0;
            padding-right: 0; 
            width: $desktop-layout-width;
            display: inline-grid;
            grid-column-gap: $column-gap;

            grid-template-columns: repeat(12, 1fr);
            grid-template-rows: 1fr;
        }

        &__text {
            text-align: left;
            grid-column: span 8;
        }

        &__illustration {
            grid-column: span 4;
        }

        //
        // Modifiers
        //
        &.-illustration-bottom {
            .o-article-frame-module__illustration {
                align-self: end;
            }
        }

        &.-illustration-center {
            .o-article-frame-module__illustration {
                align-self: center;
            }
        }

        &.-text-center {
            .o-article-frame-module__text {
                align-self: center;
            }
        }

        &.-illustration-left {
            .o-article-frame-module__illustration {
                grid-column: 1 / span 4;
                grid-row: 1;
            }

            .o-article-frame-module__text {
                grid-column: 5 / span 8;
                grid-row: 1;
            }
        }


    }
}