.a-midi-title {
    margin: 0 0;
    padding: 0 0;
    line-height: $text-line-height;
    font-family: $text-font-family;
    text-transform: uppercase;
    font-size: 1.5em;
    color: $light-text-color;

    //
    // Modifiers
    //
    &.-products-bg {
        color: $products-bg-text;
    }
}

//
// Media query lg up
//
@include media-breakpoint-up(lg) {
    .a-midi-title {
        font-size: 1.875em;
    }    
}