//
// Most generic HTML for CMS purposes
//
.m-article-body {
    // All h's
    @for $i from 1 through 6 {
        h#{$i} {
            margin: 0 0 $horizontal-base-width 0;
            padding: 0 0;
        }

        h#{$i}:not(:first-of-type) {
            margin-top: $horizontal-base-width * 2;
        }
    }

    p:not(:last-of-type) {
        margin: 0 0 $horizontal-base-width / 2 0;
        padding: 0 0;
    }

    p {
        font-size: $base-text-size-mobile;
    }
}

//
// Media query md up
//
@include media-breakpoint-up(md) {
    .m-article-body {
        p {
            font-size: $base-text-size-desktop;
        }      
    }
}