.m-vertical-advert {
    display: inline-block;

    &__header {
        text-align: left;
    }

    &__creation {
        display: flex;
    
        img {
            width: 100%;
            height: auto;
        }

        &.-border {
            border: solid 1px $border-color;
        }
    }
}

//
// Media query lg up
//
@include media-breakpoint-up(lg) {
    .m-vertical-advert {    
        //
        // Modifiers
        //
        &.-limit-66p {
            max-width: 66%;
        }

        &.-limit-height {
            max-height: 50vh;
        }
    }
}