// A Hassle-Free Way to Self-Host Google Fonts - https://google-webfonts-helper.herokuapp.com/fonts

/// Font path
/// @group Fonts
/// @type Path
$font-path: '../../fonts/' !default;

///
/// Generate local font-face
///
/// @group Fonts
///
/// @example Basic usage
///  @font-face {
///      @include fontFaceLocal('BrunelPoster-Roman', 'BrunelPoster', 'normal', 400);
///  }
///
@mixin fontFaceLocal($font, $family, $style, $weight) {
  font-family: $family;
  src: url($font-path + $font + '.eot');
  src: url($font-path + $font + '.eot?#iefix') format('embedded-opentype'),
    url($font-path + $font + '.woff') format('woff'),
    url($font-path + $font + '.ttf') format('truetype'),
    url($font-path + $font + '.woff2') format('woff2'),
    url($font-path + $font + '.svg#' + $font) format('svg');
  font-weight: $weight;
  font-style: $style;
}

// 
// Include fonts
//
@each $font in $fonts {
  @font-face {
    @include fontFaceLocal(nth($font, 1), nth($font, 2), nth($font, 3), nth($font, 4));
  }
}