.a-post-title {
    color: White;
    line-height: $text-line-height;
    font-weight: 400;
    margin: 0 0;
    padding: 0 0;

    font-size: 1.563em;

    //
    // Modifiers
    //
    &.-white {
        color: White;
    }
}

//
// Media query xl up
//
@include media-breakpoint-up(xl) { 
    .a-post-title {
        font-size: 1.875em;
    }
}