.t-front {
    &__header {
        padding-top: $horizontal-base-width;
        padding-bottom: $horizontal-base-width;

        padding-left: $vertical-base-width;
        padding-right: $vertical-base-width;
    }

    &__slider {
        padding-top: 0;
        padding-bottom: $horizontal-base-width;
    }

    &__articles-block {
        padding-left: $vertical-base-width;
        padding-right: $vertical-base-width;

        padding-top: $horizontal-base-width * 1.5;
        padding-bottom: $horizontal-base-width * 1.5;
    }

    &__vertical-advert-block {
        padding-bottom: 0;
        padding-top: $horizontal-base-width;

        padding-left: $vertical-base-width;
        padding-right: $vertical-base-width;

        > * {
            width: 100%;
        }
    }

    &__two-articles-block {
        padding-left: $vertical-base-width;
        padding-right: $vertical-base-width;

        padding-top: $horizontal-base-width;
        padding-bottom: $horizontal-base-width * 1.5;

        &__title {
            text-align: left;
        }
    }

    &__vertical-category-list {
        padding-top: $horizontal-base-width;
        padding-bottom: $horizontal-base-width * 2;
    }


    &__recommended-article {
        padding-left: $vertical-base-width;
        padding-right: $vertical-base-width;
        padding-bottom: $horizontal-base-width * 2;
    }

    //
    // Products
    //
    &__products {
        margin-bottom: $horizontal-base-width * 2;
    }

    &__advert-articles {
        padding-left: $vertical-base-width;
        padding-right: $vertical-base-width;
        padding-bottom: $horizontal-base-width * 2;

        &__advert {
            margin-bottom: $horizontal-base-width;

            > * {
                width: 100%;
            }
        }
    }
}

//
// Media query md up
//
@include media-breakpoint-up(md) {
    .t-front {
        &__header {
            > * {
                max-width: $desktop-layout-width;
            }

            padding-left: 0;
            padding-right: 0;
        }

        &__articles-block {
            padding-top: $horizontal-base-width * 4;

            > * {
                max-width: $desktop-layout-width;
            }
        }

        &__vertical-advert-block {
            padding-left: 0;
            padding-right: 0;

            > * {
                max-width: $desktop-layout-width;
                // width: $desktop-layout-width;
            }
        }

        &__two-articles-block {
            padding-top: $horizontal-base-width * 1.5;

            &__title {
                display: inline-block;
                margin-bottom: $horizontal-base-width / 2;
            }

            > * {
                width: $desktop-layout-width;
            }
        }

        &__vertical-category-list {
            > * {
                width: $desktop-layout-width;
            }
        }

        &__recommended-article {
            > * {
                width: $desktop-layout-width;
            }
        }

        &__advert-articles {
            &__container {
                display: inline-grid;
                grid-template-columns: repeat(12, 1fr);
                width: 100%;
                text-align: left;
                width: $desktop-layout-width;
                grid-column-gap: $column-gap;
            }

            &__advert {
                grid-column: span 4;
            }

            &__articles {
                grid-column: span 8;
            }
        }
    }     
}

//
// Media query lg up
//
@include media-breakpoint-up(lg) {
    .t-front {
        &__header {
            padding-top: $horizontal-base-width;
            padding-bottom: $horizontal-base-width;
        }
    
        &__slider {
            padding-top: $horizontal-base-width;
            padding-bottom: $horizontal-base-width;
        }

        &__vertical-category-list {
            padding-top: $horizontal-base-width * 3;
            padding-bottom: $horizontal-base-width * 4;
        }
    }     
}