.o-category-list {
    width: 100%;
    display: inline-grid;
    
    grid-template-columns: repeat(12, 1fr);

    grid-row-gap: $row-gap * 2;
    grid-column-gap: $column-gap;

    &__column {
        grid-column: span 6;

        &.-centered {
            grid-column: span 12;
        }
    }
}

//
// Media query lg up
//
@include media-breakpoint-up(lg) { 
    .o-category-list {    
        grid-row-gap: $row-gap;
        grid-column-gap: $column-gap;
            
        grid-template-columns: repeat(10, 1fr);
    
        &__column {
            grid-column: span 2;

            &.-centered {
                grid-column: span 2;
            }
        }
    }
}