.o-article-block {
    width: 100%;
    display: inline-grid;

    grid-template-columns: repeat(12, 1fr);
    // grid-template-rows: 1fr;

    grid-row-gap: $row-gap;
    grid-column-gap: $column-gap;

    &__article {
        grid-column: span 12;
        // margin-bottom: $horizontal-base-width * 2;
    }

    &__block-link {
        text-decoration: none;
        display: block;
    }

    &_header {
        margin-top: $horizontal-base-width / 2;

        /* > * {
            pointer-events: none;
        } */
    }
}

//
// Media query md up
//
@include media-breakpoint-up(md) { 
    .o-article-block { 
        //
        // Modifiers
        //
        &.-three-columns {
            .o-article-block__article {
                grid-column: span 6;
            }
        }

        &.-two-columns {
            .o-article-block__article {
                grid-column: span 6;
            }
        }
    }
}


//
// Media query lg up
//
@include media-breakpoint-up(lg) { 
    .o-article-block { 
        //
        // Modifiers
        //
        &.-three-columns {
            .o-article-block__article {
                grid-column: span 4;
            }
        }
    }
}