.m-post-cover {
    position: relative;

    display: block;

    width: 100%;
    overflow: hidden;

    margin-block-start: 0;
    margin-block-end: 0;
    margin-inline-start: 0;
    margin-inline-end: 0;

    &__bg {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;

        display: flex;
        z-index: 0;

        img {
            object-fit: cover;
            object-position: center center;
            width:100%; 
            height:100%;
        }
    }

    &__content {
      width: 100%;
      position: absolute;
      bottom: $horizontal-base-width * 1.5;
      z-index: 2;

      padding: 0 $vertical-base-width;
    }

    &__category {
      margin-bottom: $horizontal-base-width / 2;
    }

    &__title {
      text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.21);
    }

    &__copyright {
      width: auto;
      display: inline-block;
      position: absolute;
      bottom: 0;
      right: $vertical-base-width / 2;
      z-index: 2;
      color: White;

      text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.21);
    }

    //
    // Modifiers
    //
    &.-square {
      &::after {
        content: "";
        display: block;
        padding-bottom: 100%;
      }  
    }

    &.-aspect-47-18 {
      &::after {
        content: "";
        display: block;
        padding-bottom: 38%;
      }  
    }

    &.-aspect-64-39 {
      &::after {
        content: "";
        display: block;
        padding-bottom: 61%;
      }  
    }

    &.-hover-effect {
      .m-post-cover__bg {
        transition: transform .5s ease-in-out,
          opacity .25s ease-in-out;
      }

      &.-hover,
      &:hover {
        .m-post-cover__bg {
          transform: scale(1.2);
          opacity: .8;
        }
      }
    }
}

//
// Media query lg up
//
@include media-breakpoint-up(lg) { 
  .m-post-cover {
    &__content {
      text-align: left;
      padding: 0 0;
    }

    &__title {
      padding-left: .5em;
      padding-right: .5em;

      &.-center {
        text-align: center;
      }
    }

    //
    // Modifiers
    //
    &.-left-on-desktop {
      text-align: left;
    }
  }
}


//
// Media query xl up
//
@include media-breakpoint-up(xl) { 
  .m-post-cover {
    &__content {
      bottom: $horizontal-base-width * 2;
    }

    &__category {
      min-width: 66%;
      padding-left: 1.5em;
    }

    &__title {
      font-size: 2.188em;
      padding-left: 1em;
      padding-right: 1em;

      &.-sm {
        font-size: 1.875em;
      }
    }
  }
}