.a-post-category {
    text-transform: uppercase;
    font-size: 1.313em;
    display: inline-block;
    line-height: 100%;
    color: White;
    padding: .5em 1em .25em 1em;

    //
    // Modifiers
    //
    @each $name, $color in $category-colors {
        &.-#{$name} {
            background-color: $color;
        } 
    }
}

@include media-breakpoint-up(xl) { 
    .a-post-category {
        font-size: 1.563em;
    }
}