.m-external-post-description {
    text-align: left;


    &__title {
        color: $text-color;
        margin-bottom: $horizontal-base-width / 2;
    }

    &__text {
        color: $text-color;
        font-size: $base-text-size-mobile;
        p {
            margin: 0 0;
        }
    }
}

//
// Media query lg up
//
@include media-breakpoint-up(lg) { 
    .m-external-post-description {

    
        &__title {
            margin-bottom: $horizontal-base-width;
        }

        &__text {
            font-size: $base-text-size-desktop;
        }
    }
}