.a-nav-header {
    font-family: $text-font-family;
    font-weight: 500;
    font-size: 1em;
    color: $text-color;

    margin: 0 0;
    padding: 0 0;

    border-bottom: solid 2px Black;

    //
    // Modifiers
    //
    @each $name, $color in $category-colors {
        &.-#{$name} {
            border-bottom-color: $color;
        } 
    }
}

//
// Media query md up
//
@include media-breakpoint-up(lg) {
    .a-nav-header {
        font-size: 1.25em;
        text-align: left;
    } 
}