.m-category-icon {
    display: inline-block;
    text-decoration: none;

    color: $text-color;

    &__title {
        font-size: 1.250em;
        text-transform: uppercase;
        padding: 0 0;
        margin: 0 0;
    }

    &__graphic {
        margin-bottom: $horizontal-base-width;
        transition: opacity .5s ease-in-out;
    }

    &:hover,
    &:focus {
        .m-category-icon__graphic {
            opacity: 0.6;
        }
    }
}