.o-recommended-article {
    width: 100%;
    display: inline-block;

    &__link {
        display: block;
        text-decoration: none;
    }

    &__illustration {

    }

    &__description {
        margin-top: $horizontal-base-width / 2;
    }

    &__category-mobile {
        text-align: left;
    }

    &__category {
        display: none;
    }
}


//
// Media query md up
//
@include media-breakpoint-up(lg) { 
    .o-recommended-article { 
        &__link {
            width: 100%;
            display: inline-grid;
            grid-template-columns: repeat(12, 1fr);
            // grid-template-rows: 1fr;
        
            grid-row-gap: $row-gap;
            grid-column-gap: $column-gap;

            grid-template-rows: minmax(20px, auto) minmax(80%, auto);
        }

        &__illustration {
            grid-column: span 7;
            grid-row: span 2;
        }
    
        &__description {
            margin-top: 0;
            grid-column: span 5;
            grid-row: span 1;
            align-self: center;

            transform: translateY(-10%);
        }

        &__category {
            display: block;
            grid-column: span 5;
            grid-row: span 1;
            text-align: left;
            align-self: start;
        }

        &__category-mobile {
            display: none;
        }

    }
}