.o-article-text-module {
    overflow: auto;

    padding-left: $vertical-base-width;
    padding-right: $vertical-base-width;

    padding-top: $horizontal-base-width * 1.5;
    padding-bottom: $horizontal-base-width * 1.5;

    // Part with the text
    &__text {
        text-align: left;
    }

    // Advert amd other modules
    &__aside {
        padding: $vertical-base-width / 2 0;
        margin: $vertical-base-width 0;
        border-top: solid 1px $border-color;
        border-bottom: solid 1px $border-color;

        > * {
            width: 100%;
        }
    }
}

//
// Media query md up
//
@include media-breakpoint-up(md) {
    .o-article-text-module {

        max-width: $desktop-layout-width;
        display: inline-grid;
        padding-left: 0;
        padding-right: 0;

        grid-template-columns: repeat(12, 1fr);
        grid-template-rows: 1fr;
    
        grid-row-gap: 0;
        grid-column-gap: $column-gap;

        width: 100%;

        &__text {
            grid-column: span 9;
        }

        &__aside {
            border: none;
            grid-column: span 3;
        }

    }

}