.m-article-teaser {
    text-align: left;
    color: $text-color;

    font-size: $base-text-size-mobile;

    p {
        margin: 0 0 1em 0;
    }

    p:last-of-type {
        margin: 0 0;
    }
}

//
// Media query lg up
//
@include media-breakpoint-up(lg) { 
    .m-article-teaser {    
        font-size: $base-text-size-desktop;
    }
}