//
// Layout
//

/// Horizontal base widh
/// @group Layout
/// @type Width
$horizontal-base-width: 16px;

/// Vertical base widh
/// @group Layout
/// @type Width
$vertical-base-width: 16px;

/// Available breakpoints
/// @group Layout
/// @type Map
$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 1200px,
  xl: 1500px,
);

/// Layout width
/// @group Layout
/// @type Width
$desktop-layout-width: 55vw;

/// Column gap
/// @group Layout
/// @type Width
$column-gap: $horizontal-base-width;

/// Row gap
/// @group Layout
/// @type Width
$row-gap: $vertical-base-width;

/// Cover header limitter
/// @group Layout
/// @type Width
$cover-header-width: 650px;


//
// Paths
//

/// Font path
/// @group Paths
/// @type Path
$font-path: '../../fonts/';

/// Image path
/// @group Paths
/// @type Path
$image-path: '../../images/';





//
// Fonts
//

/// Header font family
/// @group Fonts
/// @type FontFamily
$header-font-family: 'Abril Fatface', cursive;

/// Text font family
/// @group Fonts
/// @type FontFamily
$text-font-family: 'Blogger Sans', Helvetica, sans-serif;

/// Include these fonts
/// @group Fonts
/// @type List
$fonts: 'abril-fatface-v12-latin-ext_latin-regular' 'Abril Fatface' 'normal' 400,
        'BloggerSans-Italic' 'Blogger Sans' 'italic' 400,
        'BloggerSans' 'Blogger Sans' 'normal' 400,
        'BloggerSans-MediumItalic' 'Blogger Sans' 'italic' 500,
        'BloggerSans-Medium' 'Blogger Sans' 'normal' 500;





//
// Typography
//

/// Header line height
/// @group Fonts
/// @type Height
$header-line-height: 130%;

/// Header font weight
/// @group Fonts
/// @type Weight
$header-font-weight: 400;

/// Text line height
/// @group Fonts
/// @type Height
$text-line-height: 150%;

/// Base font size
/// @group Fonts
/// @type Height
$base-text-size-mobile: 1rem;

/// Base font size
/// @group Fonts
/// @type Height
$base-text-size-desktop: 1.063rem;

// Fluid font calculation

/// Min window width for font size calculation
/// @group Fonts
/// @type Width
$min-width: 320px;

/// Max window width for font size calculation
/// @group Fonts
/// @type Width
$max-width: 1400px;

/// Min font size
/// @group Fonts
/// @type Size
$min-font: 14px;

/// Max font size
/// @group Fonts
/// @type Size
$max-font: 16px;




//
// Colors
//

/// Main color
/// @group Colors
/// @type Color
$main-color: #73a1cf;

/// Main color light
/// @group Colors
/// @type Color
$main-color-light: #e0eaf4;

/// Menu color
/// @group Colors
/// @type Color
$menu-color: #6a6b6d;

/// Overlay color
/// @group Colors
/// @type Color
$overlay-color: #73a1cf;

/// Text color
/// @group Colors
/// @type Color
$text-color: #2b2e31;

/// Light text color
/// @group Colors
/// @type Color
$light-text-color: #a8a9aa;

/// Border color
/// @group Colors
/// @type Color
$border-color: rgb(163, 163, 163);

/// Products bg color
/// @group Colors
/// @type Color
$products-bg: #6497ca;

/// Products bg text color
/// @group Colors
/// @type Color
$products-bg-text: #91b3d7;

/// Footer nav part
/// @group Colors
/// @type Color
$footer-nav: #f6f7f9;

/// Footer foot part
/// @group Colors
/// @type Color
$footer-foot: #dee1ea;

/// Footer foot part
/// @group Colors
/// @type Color
$teaser-bg: #e5e7ee;

/// Category colors
/// @group Colors
/// @type Map
$category-colors: (
  "imunitate": $products-bg,
  "ingrijrea": #f59562
);
