/* m-search-box molecule file */
.m-search-box {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: stretch;
    align-items: flex-start;

    &__input {
        order: 0;
        flex: 1 1 auto;
        align-self: auto;
    }

    &__submit {
        order: 0;
        flex: 0 1 auto;
        align-self: auto;
    }
}